  :root{
    --dark-bg: rgba(15, 15, 15, 0.95);
    --light-bg: rgba(240, 240, 240, 0.95);
    --spacing: 350px;
  }
  *{
    padding:0;
    margin:0;
    box-sizing: border-box;
    font-family: 'Space Mono', monospace;
  }
  html,
  body,
  #root {
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto,
      segoe ui, arial, sans-serif;
  }

  canvas {
    top:0;
    left:0;
    position: fixed;
  }
  .main {
    top:0;
    left:0;
    position: absolute;
    z-index: 99;
    padding-top: 30vh;
    display: grid;
    grid-template-columns: repeat(12,1fr);
    margin-bottom: 10rem;
  }
  header {
    background-color: var(--dark-bg);
    padding:2rem;
    color:white;
    grid-column: 2/ span 5;
    margin-bottom: var(--spacing);
  }
  section{
    grid-column: 2/8;
    margin-bottom: var(--spacing);
    background-color: var(--dark-bg);
    padding: 2rem;
    color:white;
  }

  h1,h2,h3{
    padding-bottom:2rem;
  }
  .right {
    grid-column: 6/12;
  }
  .socials {
    grid-column: 2/5;
    margin-bottom: 0;
    p {
      padding:0;
    }
    background-color: var(--light-bg);
    color: black;
  }
  .socials::after{
    font-size: 1.1rem;
    content: "Email: kserdult@gmail.com";
    max-width: 1;
    padding-bottom: 80vh;
    pointer-events: none;
  }
  .light{
    background-color: var(--light-bg);
    color:black;
  }
  p{
    font-size: 1.1rem;
    padding:1rem;
  }
  .blank {
    position: absolute;
    width: 100vw;
    height: fit-content;
    z-index: 200;
    background-color: white;
  }
@media screen and (orientation:portrait) {
  html, body {
    background: black;
    height: 100%;
    width: 100%;
  }
  
  .portrait {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
  }
  
  .phone {
    height: 50px;
    width: 100px;
    border: 3px solid white;
    border-radius: 10px;
    animation: rotate 1.5s ease-in-out infinite alternate;
  }
  
  .message {
    color: white;
    font-size: 1em;
    margin-top: 40px;
    padding: 0 2rem;
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0deg)
    }
    50% {
      transform: rotate(-90deg)
    }
    100% {
      transform: rotate(-90deg)
    }
  }
  main{
    display: none;
  }
  .canvas{
    display: none;
  }
}
@media screen and (orientation:landscape) {
  .portrait{
    display: none;
  }
  @media screen and (max-width: 800px) {
    h1,h2,h3{
      font-size: 1.1rem;
      padding:.5rem;
    }
    header {
      grid-column: 2/5;
    }
    p{
      font-size: 0.9rem;
    }
    section {
      margin-bottom: 200px;
      padding:0.5rem;
    }
    .socials{
      grid-column: 2/auto;
    }
    .socials::after{
      padding-bottom: 15rem;
    }
  }
  @media screen and (max-width: 1000px) {
    h1,h2,h3{
      font-size: 1.2rem;
    }
    header {
      grid-column: 2/5;
    }
    p{
      font-size: 1rem;
    }
    section {
      margin-bottom: 200px;
      padding:1.1rem;
    }
    .socials{
      grid-column: 2/auto;
    }
    .socials::after{
      padding-bottom: 30rem;
    }
  }
}